body {
  background-color: #cae6f1; }

.header {
  display: flex;
  background-color: white;
  height: 90px;
  position: fixed;
  width: 100vw;
  z-index: 2; }

.header__text {
  margin: auto; }

.sidebar {
  padding: auto;
  background-color: #8ec3d8;
  height: 100vh;
  font-size: 28px;
  line-height: 28px;
  width: 270px;
  position: fixed;
  top: 0;
  z-index: 1;
  left: -270px;
  padding: 50px 0 0 50px;
  transition: 0.5s ease;
  color: #0b2675; }
  .sidebar h3 {
    color: #092472; }
  .sidebar li {
    cursor: pointer;
    padding: 5px; }
  .sidebar .active {
    text-decoration: underline; }
  .sidebar.show {
    left: 0; }

.menu__categories.active .subcategories {
  display: block; }

.subcategories {
  display: none; }
  .subcategories li {
    font-size: 24px;
    line-height: 24px;
    color: #0b1c50; }

.header__burger {
  width: 45px;
  z-index: 2;
  position: fixed;
  margin: 20px;
  cursor: pointer; }
  .header__burger span {
    transition: 0.3s ease;
    display: block;
    width: 100%;
    height: 5px;
    margin-bottom: 10px;
    border-radius: 5px;
    background-color: #2243a8; }

.open span:nth-child(1) {
  height: 45px;
  position: relative; }

.open span:nth-child(n+2) {
  height: 5px;
  background-color: #8ec3d8;
  position: relative; }

.open span:nth-child(2) {
  transform: rotate(45deg);
  top: -36px; }

.open span:nth-child(3) {
  transform: rotate(135deg);
  top: -51px; }

.rotate {
  position: absolute;
  width: 30px;
  margin-top: -50px;
  margin-left: 88%;
  cursor: pointer;
  backface-visibility: hidden; }

.header__switcher {
  position: relative;
  background-color: rebeccapurple;
  height: 45px;
  width: 100px;
  top: 10px;
  right: 40px;
  border-radius: 23px; }
  .header__switcher input {
    opacity: 0; }
  .header__switcher .switch-handle {
    display: block;
    position: absolute;
    background-color: salmon;
    top: 3px;
    left: 3px;
    height: 38px;
    width: 38px;
    border-radius: 30px;
    transition: 0.6s ease; }
  .header__switcher .switch-label::after {
    content: attr(data-off);
    position: absolute;
    height: 38px;
    top: 15px;
    left: 50px;
    color: black;
    transition: 0.6s ease; }
  .header__switcher.check .switch-handle {
    left: 58px; }
  .header__switcher.check .switch-label::after {
    content: attr(data-on);
    position: absolute;
    height: 38px;
    top: 13px;
    left: 15px;
    color: seagreen;
    transition: 0.6s ease; }

.button {
  display: none;
  text-align: center;
  margin: 30px;
  width: 100%;
  font-size: 27px;
  line-height: 32px; }
  .button.game {
    transition: 0.6s ease;
    display: inline-block; }
  .button .button_start {
    transition: 0.3s ease;
    outline: none;
    border-radius: 10px;
    width: 350px;
    height: 60px; }
    .button .button_start.repeat {
      transition: 0.3s ease;
      border-radius: 50%;
      width: 60px; }

.sort, .restart {
  width: 100%; }

.statistic-button {
  padding: 0 40px;
  font-size: 27px;
  line-height: 32px;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 100px;
  top: 0;
  display: flex;
  position: absolute;
  z-index: 1;
  width: 100%; }
  .statistic-button .button-reset, .statistic-button .button-diff {
    margin: 10px;
    background-color: white;
    border-radius: 5px;
    width: 250px;
    outline: none;
    height: 40px;
    border: 0;
    box-shadow: 3px 3px 2px rgba(34, 60, 80, 0.2); }

.content {
  font-size: 27px;
  line-height: 32px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 90px 40px 40px; }
  .content.statistic {
    padding-top: 150px; }

.result-lose {
  background: url(e9de6a62a86ff131af6f5886fed182a6.jpg); }
  .result-lose .result-text {
    text-align: center; }
    .result-lose .result-text .error {
      margin: 0px; }

.result-win {
  background: url(dd613f05ce17855bb55670783a793d48.jpg); }

.result-win, .result-lose {
  z-index: 2;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: center;
  background-position-y: center;
  height: 100vh; }

.card, .card-categories {
  position: relative;
  width: 300px;
  height: 350px;
  margin: 20px;
  transition: 0.6s ease;
  cursor: pointer; }
  .card .image, .card-categories .image {
    width: 100%;
    height: 300px;
    object-fit: cover; }
  .card .card_text, .card-categories .card_text {
    height: 50px;
    text-align: center; }
  .card.translate .front, .card-categories.translate .front {
    transform: rotateY(180deg); }
  .card.translate .back, .card-categories.translate .back {
    transform: rotateY(0deg); }

.card.play {
  height: 300px;
  box-shadow: 3px 3px 5px #525050;
  border-radius: 5%;
  overflow: hidden; }

.card.inactive {
  opacity: 0.6;
  cursor: no-drop; }

.card-categories.play {
  transition: 0.6s ease; }
  .card-categories.play::before, .card-categories.play::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 5%;
    background: #fff;
    z-index: -1; }
  .card-categories.play::before {
    transform: rotate(10deg);
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2); }
  .card-categories.play::after {
    transform: rotate(5deg);
    box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2); }

.front, .back {
  position: absolute;
  overflow: hidden;
  border-radius: 5%;
  background-color: white;
  box-shadow: 3px 3px 5px #525050;
  transition: 0.3s ease-in;
  backface-visibility: hidden; }
  .front:hover, .back:hover {
    box-shadow: 3px 6px 10px #0f0d0d; }

.front {
  transform: rotateY(0deg); }

.back {
  transform: rotateY(180deg); }

.card-stat {
  height: 250px;
  width: 300px;
  background-color: white;
  border-radius: 25px;
  overflow: hidden;
  margin: 20px;
  position: relative; }
  .card-stat img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    opacity: 0.2; }
  .card-stat .card-stat__text {
    position: absolute;
    width: 100%;
    top: 20px;
    text-align: center; }
    .card-stat .card-stat__text .word {
      font-size: 35px;
      line-height: 35px; }
    .card-stat .card-stat__text p {
      margin: 3px; }

.star {
  padding-top: 5px;
  height: 42px;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: flex-end; }

.ul-statistics {
  background-color: white;
  border-radius: 5px;
  box-shadow: 3px 3px 2px rgba(34, 60, 80, 0.2);
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  width: 250px;
  min-height: 40px;
  margin: 10px; }
  .ul-statistics.active {
    box-shadow: 3px 3px 2px rgba(34, 60, 80, 0.2) inset; }
    .ul-statistics.active li {
      display: block; }
  .ul-statistics li {
    border-radius: 5px;
    background-color: #cfcff3;
    margin: 5px;
    display: none; }
